.testimonial {
  &__photo-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;
    margin-bottom: 28px;
  
    @include onDesktop {
      margin-bottom: 32px;
    }
  }
  
  &__photo {
    width: 100%;
    grid-column: 2 / 4;
    border-radius: 50%;
  }
  
  &__name {
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    margin: 0 0 4px;
  
    &-link {
      text-decoration: none;
      color: $colorBlue;
    }
  }
  
  &__role {
    margin: 0 0 4px;
    font-weight: normal;
    font-size: 14px;
    line-height: 27px;
    text-align: center;
    color: #9FADC2;
  }
  
  &__about {
    text-align: center;
  }
}
